import React from 'react';
import { generateRandomString } from '../util.js';

const Listgroup = props => {

    let { titleId, title, ...otherProps } = props;

    if ( !titleId ) {

        titleId = 'listgroup-' + generateRandomString()
    }

    return  <div className="rocket__listgroup" aria-describedby={ titleId } { ...otherProps }>
                <div className="rocket__listgroup__title" id={ titleId }>{ props.title }</div>
                <div className="rocket__listgroup__group" role="list">
                    { props.children }
                </div>
            </div>
}

const Item = props => {

    return  <div className="rocket__listgroup__item" role="listitem" { ...props } >{ props.children }</div>
}

Listgroup.defaultProps = {

    title: ''
}

Listgroup.Item = Item;

export {

    Listgroup
}